

//blog individual page ----title header
.header .banner{
  margin-bottom: 30px;
}

.individual-page{


  .region-content{
    width: 75%;
    float: left;

  }
  aside.layout-sidebar{
    width: 25%;
    float: left;
  }

}
.layout-content-sec{
  clear: both;
}


//blog individual page --- sub title -- topic

.blog-full-title{
  margin: 15px 0;
  @include phone{
    font-size: 24px;
  }
}

//blog individual page --- content
.blog-full-content{

     margin-right: 15px;



  .field--name-field-blog-topics-covered > .field--item{
      display: inline-block;
      margin-top: 25px;
      margin-bottom: 75px;
      a{
        color: $tag-yellow;
        font-size: 27px;
      }

        &::after{
          content: "  |  ";
          color: $tag-yellow;
          font-size: 45px;
        }
        &:last-of-type{
          &::after{
            content: " ";

            font-size: $font-size-text;
          }
        }
      } //field==item

    .field--name-field-blog-date{
        font-size: $font-size-text;
        font-style: italic;
        color: $font-grey;

    }
    .field--name-body{
      font-size: $font-size-subheading;
      font-weight: bold;
      margin: 10px 0 30px 0;
    }

  }

  //author information

  .author-info{
    margin-bottom: 55px;
      .field--name-user-picture{
        float: left;
        img{
          -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
        }
      }
      .field--name-field-bio{
        padding-left: 120px;
        padding-top: 15px;
        p{
          font-size: $font-size-text;
          font-style: italic;
          color: $font-grey;
        }
      }
  }

 
